html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body{
    font-family: $fuentePrincipal;
    font-size: 1.6rem;
    font-weight: $delgada;

   
    
}


.contenedor{
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
  
}
a { 
    text-decoration: none;
}
 img{
     max-width: 100%;
 }
h1{
    font-size: 4rem;
    font-weight: $delgada;
}
h2{
    font-size: 3.4rem;
}
h3{
    font-size: 2.8rem;
}