
.galeria-discos {
   
    
   display: grid;
   grid-template-columns: 1;
   
   

    @include telefono{
        @include grid (2, 2rem)
    }
    @include tablet {
        @include grid (4, 2rem)
    }
    @include desktop {
        @include grid (6, 2rem)
    }
    
    text-align: center;
    align-items: center;
    justify-content: center;
}

.discos-galeria{
   margin: auto;
   max-width: 70%;
   color: $negro;

    p{
        font-size: 1.5rem;
        font-weight: $media;
        margin: 5px;
        color: $grisFuerte;
        text-align: center;
        

    }
    .disco-nombre{
        margin-bottom: 5px;
    }
    
    .boton-galeria{
        justify-content: space-between;
    }
   
    .boton-galeria__spotify{
        background-color: $blanco;
        padding: 1rem;
        border-radius: .5rem;
        color: $grisFuerte;
        margin-top: .5rem;
        margin-right: 10%;
        &:hover{
            transition: all 1s ease;
            background-color: $verde;
            color: $blanco;
        }
        
    } 
    
    .boton-galeria__youtube{
        background-color: $blanco;
        padding: 1rem ;
        
        margin-top: .5rem;
        border-radius: .5rem;
        color: $grisFuerte;
        &:hover{
            transition: all 1s ease;
            background-color: $rojo;
            color: $blanco;
        }
    } 
    
    
}


.titulo-discos{
    h3{
        background-image: url(../../../build/img/galeria/12.webp);
        background-position: bottom left;
        background-repeat: no-repeat;
        background-size:auto;
        text-align: center;
        font-weight: $media;
        margin: 0;
        padding: 2rem;
        color:$blanco;
        text-transform: uppercase;
    }
}
