.galeria-imagenes{
    width: 60%;
   margin: 0 auto;
   border: 5.2px solid $grisFuerte;
   overflow: hidden;
   border-radius: 1rem;
}
.galeria-slide{
    display: flex;
    width: 100%;
    

}


    #prevBtn{
        position: absolute;
        left: 3%;
        bottom: 20%;
        font-size: 15px;
        border-radius: 50px;
        border: none;
        cursor: pointer;
        opacity: .7;
    }   
    #nextBtn{
        position: absolute;
        right: 3%;
        bottom: 20%;
        font-size: 15px;
        border-radius: 50px;
        border: none;
        cursor: pointer;
        opacity: .7;
    }
@include tablet{
    #prevBtn{
        position: absolute;
        left: 5%;
        bottom: 7%;
        font-size: 30px;
        border-radius: 50px;
        border: none;
        cursor: pointer;
        opacity: .7;
    }   
    #nextBtn{
        position: absolute;
        right: 5%;
        bottom: 7%;
        font-size: 30px;
        border-radius: 50px;
        border: none;
        cursor: pointer;
        opacity: .7;
    }
}
