.footer{
    background-color: $grisFooter;
    display: flex;
    justify-content: center;
    p{
    text-align: center;
    font-size: 1.6rem;
    font-weight: $delgada;
    margin: 0;
    padding: 1.5rem;
    color: $blanco;
    }
   
    
}
.facebook{
    color: #000;
    margin: 1.5rem;
    &:hover{
        transition: all .5s ease;
        color: #3B5998;
        
    }
}
.instagram{
    color: #000;
    margin: 1.5rem;
    &:hover{
        transition: all .5s ease;
        color: rgba(193,53,132,1);
        
    }
}