

    .fondo-contacto{
        
        h3{
            background-image: url(../../../build/img/galeria/12.webp);
            background-position: bottom left;
            background-repeat: no-repeat;
            background-size:auto;
            text-align: center;
            font-weight: $media;
            margin: 0;
            padding: 2rem;
            color:$blanco;
            text-transform: uppercase;
        }
    
    }
    .formulario{
        width: 95%;
        margin: 2rem auto;
        padding: 2rem;
    }
    .campo{
        display: flex;
        margin-bottom: 2rem;
    }
    .campo__label{
        flex: 0 0 7rem;
        text-align: right;
        padding-right: 1rem;
    
    }
    .campo__field{
        flex: 1;
        border: 1px solid $grisFuerte;
        border-radius: 5px;
    }
    .campo__field--textarea{
    height: 15rem;
    }
    .boton{
        display: flex;
        cursor: pointer;
        
        font-size: 2rem;
        font-weight: $delgada;
        color: $blanco;
        background-color: $negro;
        border-radius:5px;
        border: none;
        margin-left: auto;
        margin-right: auto;
    }
    form{
        background-color: $verdeHamburguesa;
        margin: 0 auto;
        border-radius: 15px;
        font-weight:$media ;
    }
    
    

