    padding-bottom: 2rem;
}
.palfijo{
    background-color: $fondoGaleria;
    width: 100%;
    height: 100%;
    text-align: center;
    padding-top: 4rem;
    .rotate{
        
            animation: girar 2s ease-in-out;
            animation-delay: 1000ms;
            @keyframes girar {  
                0%{
                    transform: rotate(0grad);
                }
                100%{
                    transform: rotate(400grad);
                }
            }
            
            width: 30%;
            @include tablet{
                width: 20%;
            }
          
    }
}