.header{
    position: fixed;
    width: 100%;
    top: 0;
    box-shadow: 0 .3rem .3rem #444;
    z-index: 8;
    
    a{
        opacity: 1;
        
    }
    
    nav{
        height: 10vh;
    }
    @include hamburguesa{

        nav{
            height: 8vh;
        }
        .linea{
            width: 30px;
            height: 3px;
            background: $blanco;
            margin: 5px;
        }
    }
    
}
.fijo{
    opacity: .6;
}
.navegacion-principal{

    background-image: url(../../../build/img/galeria/12.webp);
    background-position: bottom left;
    background-repeat: no-repeat;
    background-size:auto;
    position: relative;
    z-index: 1;
    
}

nav{
    height: 10vh;
    
      
}
ul{
    margin: 0 0;
}

.nav-links{
    display: flex;
    list-style: none;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    margin: 0 auto;
    .logo-letras{
        width: 30%;
        height: 100%;
        margin-top: 1rem;
       
    }



    li a{
        color: $blanco;
        text-decoration: none;
        font-size: 2rem;
        cursor: pointer;
        font-weight: $media;
        
    }
}

@include hamburguesa {
    .fijo{
        opacity: 1;
    }
    
    .linea{
        width: 30px;
        height: 3px;
        background: $blanco;
        margin: 5px;
    }
    nav{
        position: relative;
        
    }
    .hamburguesa{
        position: absolute;
        cursor: pointer;
        right: 5%;
        top: 50%;
        transform: translate(-5%, -50%);
        z-index: 5;
    }
    .nav-links{
        position: fixed;
        background-image: url(../../../build/img/fondoHamburg.webp);
        background-position: right;
        height: 100vh;
        width: 100%;
        flex-direction: column;
        margin: 0 auto;
        padding: 10rem;
        clip-path: circle(100px at 90% -40%);
        -webkit-clip-path: circle(100px at 90% -40%);
        transition: all 1s ease-out;
        pointer-events: none;
        
        
        
    }
    .nav-links.open{
        clip-path: circle(2500px at 90% -30%);
        -webkit-clip-path: circle(2500px at 90% -30%);
        pointer-events: all;
    }
    .nav-links li{
        opacity: 0;
    }
    .nav-links li a{
        font-size: 2.5rem;
    }
    .nav-links li:nth-child(1) {
        transition: all 0.5s ease 0.3s;
    }
    .nav-links li:nth-child(2) {
        transition: all 0.5s ease 0.6s;
    }
    .nav-links li:nth-child(3) {
        transition: all 0.5s ease 0.9s;
    }
    .nav-links li:nth-child(4) {
        transition: all 0.5s ease 1.2s;
    }
    li.fade {
        opacity: 1;
        
    }
}

