@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200;400;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Dosis", sans-serif;
  font-size: 1.6rem;
  font-weight: 200;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
  font-weight: 200;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
}

