.galeria-discos {
  display: grid;
  grid-template-columns: 1;
  text-align: center;
  align-items: center;
  justify-content: center;
}
@media (min-width: 480px) {
  .galeria-discos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-discos {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .galeria-discos {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.discos-galeria {
  margin: auto;
  max-width: 70%;
  color: #000;
}
.discos-galeria p {
  font-size: 1.5rem;
  font-weight: 400;
  margin: 5px;
  color: #999998;
  text-align: center;
}
.discos-galeria .disco-nombre {
  margin-bottom: 5px;
}
.discos-galeria .boton-galeria {
  justify-content: space-between;
}
.discos-galeria .boton-galeria__spotify {
  background-color: #fff;
  padding: 1rem;
  border-radius: 0.5rem;
  color: #999998;
  margin-top: 0.5rem;
  margin-right: 10%;
}
.discos-galeria .boton-galeria__spotify:hover {
  transition: all 1s ease;
  background-color: #1db954;
  color: #fff;
}
.discos-galeria .boton-galeria__youtube {
  background-color: #fff;
  padding: 1rem;
  margin-top: 0.5rem;
  border-radius: 0.5rem;
  color: #999998;
}
.discos-galeria .boton-galeria__youtube:hover {
  transition: all 1s ease;
  background-color: #c4302b;
  color: #fff;
}

.titulo-discos h3 {
  background-image: url(../../../build/img/galeria/12.webp);
  background-position: bottom left;
  background-repeat: no-repeat;
  background-size: auto;
  text-align: center;
  font-weight: 400;
  margin: 0;
  padding: 2rem;
  color: #fff;
  text-transform: uppercase;
}

.fondo-contacto h3 {
  background-image: url(../../../build/img/galeria/12.webp);
  background-position: bottom left;
  background-repeat: no-repeat;
  background-size: auto;
  text-align: center;
  font-weight: 400;
  margin: 0;
  padding: 2rem;
  color: #fff;
  text-transform: uppercase;
}

.formulario {
  width: 95%;
  margin: 2rem auto;
  padding: 2rem;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
}

.campo__label {
  flex: 0 0 7rem;
  text-align: right;
  padding-right: 1rem;
}

.campo__field {
  flex: 1;
  border: 1px solid #999998;
  border-radius: 5px;
}

.campo__field--textarea {
  height: 15rem;
}

.boton {
  display: flex;
  cursor: pointer;
  font-size: 2rem;
  font-weight: 200;
  color: #fff;
  background-color: #000;
  border-radius: 5px;
  border: none;
  margin-left: auto;
  margin-right: auto;
}

form {
  background-color: rgba(148, 155, 148, 0.35);
  margin: 0 auto;
  border-radius: 15px;
  font-weight: 400;
}

@media (min-width: 768px) {
  .fondo-contacto h3 {
    background-image: url(../../../build/img/galeria/12.webp);
    background-position: bottom left;
    background-repeat: no-repeat;
    background-size: auto;
    text-align: center;
    font-weight: 400;
    margin: 0;
    padding: 2rem;
    color: #fff;
    text-transform: uppercase;
  }

  .formulario {
    width: 60%;
    padding: 5rem;
  }

  .campo {
    display: flex;
    margin-bottom: 2rem;
  }

  .campo__label {
    flex: 0 0 9rem;
    text-align: right;
    padding-right: 1rem;
  }

  .campo__field {
    flex: 1;
    border: 1px solid #999998;
    border-radius: 5px;
  }

  .campo__field--textarea {
    height: 15rem;
  }

  .boton {
    display: flex;
    cursor: pointer;
    font-size: 2rem;
    font-weight: 200;
    color: #fff;
    background-color: #000;
    border-radius: 5px;
    border: none;
    margin-left: auto;
    margin-right: auto;
  }
}
.footer {
  background-color: #80807e;
  display: flex;
  justify-content: center;
}
.footer p {
  text-align: center;
  font-size: 1.6rem;
  font-weight: 200;
  margin: 0;
  padding: 1.5rem;
  color: #fff;
}

.facebook {
  color: #000;
  margin: 1.5rem;
}
.facebook:hover {
  transition: all 0.5s ease;
  color: #3B5998;
}

.instagram {
  color: #000;
  margin: 1.5rem;
}
.instagram:hover {
  transition: all 0.5s ease;
  color: #c13584;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  box-shadow: 0 0.3rem 0.3rem #444;
  z-index: 8;
}
.header a {
  opacity: 1;
}
.header nav {
  height: 10vh;
}
@media (max-width: 768px) {
  .header nav {
    height: 8vh;
  }
  .header .linea {
    width: 30px;
    height: 3px;
    background: #fff;
    margin: 5px;
  }
}

.fijo {
  opacity: 0.6;
}

.navegacion-principal {
  background-image: url(../../../build/img/galeria/12.webp);
  background-position: bottom left;
  background-repeat: no-repeat;
  background-size: auto;
  position: relative;
  z-index: 1;
}

nav {
  height: 10vh;
}

ul {
  margin: 0 0;
}

.nav-links {
  display: flex;
  list-style: none;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  margin: 0 auto;
}
.nav-links .logo-letras {
  width: 30%;
  height: 100%;
  margin-top: 1rem;
}
.nav-links li a {
  color: #fff;
  text-decoration: none;
  font-size: 2rem;
  cursor: pointer;
  font-weight: 400;
}

@media (max-width: 768px) {
  .fijo {
    opacity: 1;
  }

  .linea {
    width: 30px;
    height: 3px;
    background: #fff;
    margin: 5px;
  }

  nav {
    position: relative;
  }

  .hamburguesa {
    position: absolute;
    cursor: pointer;
    right: 5%;
    top: 50%;
    transform: translate(-5%, -50%);
    z-index: 5;
  }

  .nav-links {
    position: fixed;
    background-image: url(../../../build/img/fondoHamburg.webp);
    background-position: right;
    height: 100vh;
    width: 100%;
    flex-direction: column;
    margin: 0 auto;
    padding: 10rem;
    clip-path: circle(100px at 90% -40%);
    -webkit-clip-path: circle(100px at 90% -40%);
    transition: all 1s ease-out;
    pointer-events: none;
  }

  .nav-links.open {
    clip-path: circle(2500px at 90% -30%);
    -webkit-clip-path: circle(2500px at 90% -30%);
    pointer-events: all;
  }

  .nav-links li {
    opacity: 0;
  }

  .nav-links li a {
    font-size: 2.5rem;
  }

  .nav-links li:nth-child(1) {
    transition: all 0.5s ease 0.3s;
  }

  .nav-links li:nth-child(2) {
    transition: all 0.5s ease 0.6s;
  }

  .nav-links li:nth-child(3) {
    transition: all 0.5s ease 0.9s;
  }

  .nav-links li:nth-child(4) {
    transition: all 0.5s ease 1.2s;
  }

  li.fade {
    opacity: 1;
  }
}
.galeria-imagenes {
  width: 60%;
  margin: 0 auto;
  border: 5.2px solid #999998;
  overflow: hidden;
  border-radius: 1rem;
}

.galeria-slide {
  display: flex;
  width: 100%;
}

#prevBtn {
  position: absolute;
  left: 3%;
  bottom: 20%;
  font-size: 15px;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  opacity: 0.7;
}

#nextBtn {
  position: absolute;
  right: 3%;
  bottom: 20%;
  font-size: 15px;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  opacity: 0.7;
}

@media (min-width: 768px) {
  #prevBtn {
    position: absolute;
    left: 5%;
    bottom: 7%;
    font-size: 30px;
    border-radius: 50px;
    border: none;
    cursor: pointer;
    opacity: 0.7;
  }

  #nextBtn {
    position: absolute;
    right: 5%;
    bottom: 7%;
    font-size: 30px;
    border-radius: 50px;
    border: none;
    cursor: pointer;
    opacity: 0.7;
  }
}
.fondo-galeria {
  background-color: #e4e4e4;
  width: 100%;
  height: 100%;
  padding-top: 4rem;
  padding-bottom: 4rem;
}

.fondo-discos {
  background-color: #dadada;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.fondo-equipos {
  background-color: #d1d1d1;
  width: 100%;
  height: 100%;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.fondo-contacto {
  background-color: #c7c7c7;
  width: 100%;
  height: 100%;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.palfijo {
  background-color: #e4e4e4;
  width: 100%;
  height: 100%;
  text-align: center;
  padding-top: 4rem;
}
.palfijo .rotate {
  animation: girar 2s ease-in-out;
  animation-delay: 1000ms;
  width: 30%;
}
@keyframes girar {
  0% {
    transform: rotate(0grad);
  }
  100% {
    transform: rotate(400grad);
  }
}
@media (min-width: 768px) {
  .palfijo .rotate {
    width: 20%;
  }
}

.equipos h3 {
  background-image: url(../../../build/img/galeria/12.webp);
  background-position: bottom left;
  background-repeat: no-repeat;
  background-size: auto;
  text-align: center;
  font-weight: 400;
  margin: 0;
  padding: 2rem;
  color: #fff;
  text-transform: uppercase;
}

.pBios {
  width: 95%;
  margin: 3rem auto;
  padding: 5rem;
  text-align: justify;
  font-size: 2rem;
  font-weight: 400;
  color: #5e5d5d;
}